.page_link {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 21px;
    line-height: 1.21;
    letter-spacing: 1.73px;
    font-weight: 700;
    color: #70778b;
    height: 52px;
    width: 50px;
}
  
.active {
    position: relative;
    color: #5876c5;
}

.active::after {
    display: inline-block;
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 2.6px;
    background-color: #5876c5;
}
  
.page_item:first-child {
    border-right: 1.3px solid #dee3ef;
    opacity: 0.5;
}
  
.page_item:last-child {
    border-left: 1.3px solid #dee3ef;
    opacity: 0.5;
}
  
.pagination {
    margin-top: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #ffffff;
    box-shadow: 2px 1px 7px rgba(0, 0, 0, 0.08),
        0px 2px 1px -1px rgba(0, 0, 0, 0.04), 0px 1px 3px rgba(0, 0, 0, 0.12);
    border-radius: 10.4px;
}